<template>
</template>
<script>
//DONE:get a guest sign up
//DONE:condition the creation of guest to the presence of a email in the browser session.
//TODO:get the guest signed in
//TODO:change the navbar so it let sign in guest to their real account(change condition of acound/sign in)
import { reactive, ref } from '@vue/reactivity'
import axios from 'axios'
import { onMounted } from 'vue'
import { useRouter } from 'vue-router';
import UserProfilePic from './UserProfilePic.vue';
export default {
  setup() {
    onMounted(() => {
      if (sessionStorage.getItem("email") == null) {
        console.log("before GenerateNewGuest()")
        GenerateNewGuest()

        console.log("before loginNewGuest()")

        setTimeout(function () {
          loginNewGuest()
    }, 4000);
      }
    })
    const uniqueNb = ((parseInt(Date.now())) + '').substring(4)
    const state = reactive({
      email: "guest" + uniqueNb + "@guest.gachi",
      nickname: "guest",
      password: "guest",
      repassword: "guest",
      secretA: "guest",
      secretQ: "guest",
    });
    const GenerateNewGuest = async () => {
      const url = "http://43.201.60.125/signup/userRegister";
      const headers = {
        "Content-Type": "application/json",
        Authorization: state.token,
        "token": state.token
      };
      const body = {
        email: state.email,
        password: state.password,
        nickname: state.nickname,
        secretQ: state.secretQ,
        secretA: state.secretA,
      };
      await axios.post(url, body, { headers }).then(function (res) {
        console.log("Guest account created :" + res.data);
      }).catch(function (error) { console.log('Error', error.message) })
    };
    const loginNewGuest = async () => {
      const url = "http://43.201.60.125/members/login"
      const headers = { "Content-Type": "application/json; charset=utf-8" }
      const body = { email: state.email, pw: state.password }
      await axios.post(url, body, { headers }).then(function (res) {
        if (res.data != null) {
          sessionStorage.setItem("TOKEN", res.data.token)
          sessionStorage.setItem("email", res.data.email)
          sessionStorage.setItem("guest", true)
        } else {
        }
      }).catch(function (error) {
        //TODO: If any error send the same message.
        //could be more precise... (like 500?)
        console.log('login Error', error.message);
      })
    }

    return { uniqueNb, state, GenerateNewGuest, loginNewGuest };
  },
  components: { UserProfilePic }
};
</script>
