<template>
  <TheGuestMaker />
  <div class="container row">
    <main class="container col">
      <br>
      <h1 class="display-6 text-center">Gachi++에 오신것을 환영합니다!</h1>
      <hr>
      <article>
        <p class="lead">
          "가치++는 누구든 시간과 노력을 들여 자신이 정한 목표를 이룰 수 있도록 
          도와주는 소셜 네트워크 입니다. 
          개인의 목표가 우리의 목표가 되어 더 큰 가치를 만들어 낼 수 있습니다."
        </p>
        <br>
        <figure class="text-end">
          <blockquote class="blockquote">
            <p class="lead fs-6"> "성공한 사람이 아니라 가치있는 사람이 되기 위해 힘쓰라" <br>
              <span class="fs-7">"Try not to become a man of success but rather try to become a man of value"</span></p>
          </blockquote>
          <figcaption class="blockquote-footer">
            알버트 아인슈타인 <cite title="Source Title">Albert Einstein</cite>
          </figcaption>
        </figure>
      </article>
    </main>

    <div class="col tuto position-relative">
      <RouterLink to="/" style="text-decoration:none; color:#30353d;">
        <button type="button" class="btn btn-light btn-lg position-absolute top-0 end-0"
          style="background:rgba(6, 90, 92, 0.3);border-radius: 2em;">X</button>
      </RouterLink>
      <h3>Gachi++는 당신이 목표를 이룰 수 있도록 도와줍니다. 당신의 목표를 리스트에 추가해 보세요.</h3>
      <div class="row">
        <div class="mb-3 col col-md-6 col-sm-6 ">
          <label class="form-label">Goal Title</label>
          <input type="text" class="form-control" v-model="state.name" ref="goalname" autofocus maxlength="20">
          <div class="form-text" ref="notice">Required (*)</div>
        </div>
        <div class="mb-3 col col-md-6 col-sm-6">
          <label class="form-label">Total quantity</label>
          <input type="number" class="form-control" v-model="state.successQ" ref="goalQ" min="1" autofocus>
          <div class="form-text" ref="noticenum">Only number</div>
        </div>
      </div>
      <div class="row ">
        <div class="mb-3 col col-md-6 col-sm-6">
          <div for="inputName" class="form-label">Unit</div>
          <div class="form-check form-check-inline">
            <input class="form-check-input" type="radio" v-model="state.unit" name="inlineRadioOptions"
              id="inlineRadio1" value="Page" @input="disableButton">
            <label class="form-check-label" for="inlineRadio1">Page</label>
          </div>
          <div class="form-check form-check-inline">
            <input class="form-check-input" type="radio" v-model="state.unit" name="inlineRadioOptions"
              id="inlineRadio2" value="EA" @input="disableButton">
            <label class="form-check-label" for="inlineRadio2">EA</label>
          </div>
          <div class="form-check form-check-inline">
            <input class="form-check-input" type="radio" v-model="state.unit" name="inlineRadioOptions"
              id="inlineRadio3" value="km" @input="disableButton">
            <label class="form-check-label" for="inlineRadio3" @input="disableButton">km</label>
          </div>
          <div class="form-check form-check-inline">
            <input class="form-check-input" type="radio" v-model="state.unit" name="inlineRadioOptions"
              id="inlineRadio4" value="specialUnit" @input="textRadio">
            <input type="text" placeholder="else.." style="width: 3em;" id="inlineRadio4" ref="specialUnit" disabled>
          </div>
        </div>
        <div class="mb-3  col-md-6 col-sm-6">
          <div for="inputName" class="form-label">Choose days</div>
          <div class="form-check form-check-inline" v-for="(day, idx) in daysList" :key="idx">
            <input class="form-check-input" type="checkbox" :id="idx" v-model="checkBoxArray[idx]">
            <label class="form-check-label" :for="idx">{{day}}</label>
          </div>
        </div>
      </div>
      <div class="row">
        <!-- 요일 선택 -->
        <div class="mb-3  col-md-6 col-sm-6">
          <!-- Due Date -->
          <label for="inputName" class="">Due date</label>
          <input type="date" class="form-control" id="inputName" v-model="state.dueDate" ref="dueDate" autofocus>
          <div class="form-text" ref="noticeDate">Required (*)</div>
        </div>
        <div class="mb-3 item buttons col-md-6 col-sm-6" style="text-align:right">
          <button type="button" class="btn btn-success" @click.prevent="goalCreation"
            style="background:rgba(6, 90, 92, 0.3);border-radius: 2em;">
            <svg xmlns="http://www.w3.org/2000/svg" width="50" height="50" fill="currentColor"
              class="bi bi-arrow-return-right" viewBox="0 0 16 16">
              <path fill-rule="evenodd"
                d="M1.5 1.5A.5.5 0 0 0 1 2v4.8a2.5 2.5 0 0 0 2.5 2.5h9.793l-3.347 3.346a.5.5 0 0 0 .708.708l4.2-4.2a.5.5 0 0 0 0-.708l-4-4a.5.5 0 0 0-.708.708L13.293 8.3H3.5A1.5 1.5 0 0 1 2 6.8V2a.5.5 0 0 0-.5-.5z">
              </path>
            </svg> <span class="fs-1">저장</span>
          </button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { reactive, ref } from '@vue/reactivity'
import axios from 'axios'
import { useRouter } from 'vue-router';
import ListCircleName from '../components/SelectCircleList.vue';
import TheGuestMaker from '../components/TheGuestMaker.vue';
export default {
  data() {
    return {
      daysList: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun'],
    }
  },
  setup() {
    const router = useRouter();
    const goalname = ref('run 10km')
    const frm = ref(null)
    const pickedDays = ref(null)
    const notice = ref(null)
    const goalQ = ref('10')
    const noticenum = ref(null)
    const specialUnit = ref('km')
    const dueDate = ref(null)
    const noticeDate = ref(null)

    const checkBoxArray = ref([true, true, true, true, true, true, true])
    const state = reactive({
      name: 'run 10km',
      dueDate: '2022-10-14',
      content: 'to strat getting in shape',
      successQ: '10',
      unit: 'km',
      days: [true, true, true, true, true, true, true],
      circleid: '',
      email: sessionStorage.getItem('email'),
      token: sessionStorage.getItem("TOKEN"),
    })
    const circleid = (data) => {
      console.log(data);
      state.circleid = data;
    }
    const goalCreation = async () => {
      let now = new Date();
      console.log("now: " + now)
      if (goalname.value.value === '') {
        notice.value.innerHTML = 'Please input goal name'
        notice.value.style.color = 'red'; goalname.value.focus(); return false;
      } if (goalQ.value.value == 0 || goalQ.value.value === '') {
        noticenum.value.innerHTML = 'Please check the number'
        noticenum.value.style.color = 'red'; goalQ.value.focus(); return false;
      } if (dueDate.value.value === '') {
        notice.value.innerHTML = 'Please input due date'
        notice.value.style.color = 'red'; dueDate.value.focus(); return false;
      }
      console.log("typeof state.dueDate: " + typeof state.dueDate)
      console.log("typeof now: " + typeof now)
      console.log("goal creation is launched")
      const url = "http://43.201.60.125/api/createGoal"
      const headers = {
        "Content-Type": "application/json",
        Authorization: state.token,
        "token": state.token
      }
      const body = {
        user: state.email,
        name: state.name,
        content: state.content,
        successQ: state.successQ,
        unit: state.unit,
        days: checkBoxArray,
        dueDate: state.dueDate,
        circleid: '8',
      }
      console.log("body.user: " + body.user)
      console.log("body.name: " + body.name)
      console.log("body.days: " + body.days)
      console.log("body.successQ: " + body.successQ)
      console.log("body.dueDate: " + body.dueDate)
      console.log("body.unit:" + body.unit)
      console.log("body.circleid: " + body.circleid)
      const response = await axios.post(url, body, { headers })
      console.log("response.data" + response.data)
      if (response.status === 200) {
        console.log("body.name: " + body.name)
        alert('목표를 성공적으로 만들었습니다.');
        router.push({ name: "tutoAgenda" })
      } else { alert('fail') }


    }
    console.log("typeof state.dueDate: " + typeof state.dueDate)
    console.log("typeof now: " + typeof now)
    console.log("goal creation is launched")
    const url = "http://43.201.60.125/api/createGoal"
    const headers = {
      "Content-Type": "application/json",
      Authorization: state.token,
      "token": state.token
    }
    const body = {
      user: state.email,
      name: state.name,
      content: state.content,
      successQ: state.successQ,
      unit: state.unit,
      days: checkBoxArray,
      dueDate: state.dueDate,
      circleid: '8'
    }
    console.log("body.user: " + body.user)
    console.log("body.name: " + body.name)
    console.log("body.days: " + body.days)
    console.log("body.successQ: " + body.successQ)
    console.log("body.dueDate: " + body.dueDate)
    console.log("body.unit:" + body.unit)
    console.log("body.circleid: " + body.circleid)
    const response =async()=>{

      await axios.post(url, body, { headers }).then(() => {
        console.log("response.data" + response.data)
        console.log("body.name: " + body.name)
        alert('목표를 성공적으로 만들었습니다');
        router.push({ name: "tutoAgenda" })
      }).catch((err) => { console.log('err :' + err) });
    } 

    function cancel() {
      frm.value.reset()
      goalname.value.focus()
    }
    function textRadio() {
      this.specialUnit.disabled = false
    }
    function disableButton() {
      specialUnit.value.value = '';
    }
    return { goalname, frm, checkBoxArray, notice,noticeDate, goalQ, noticenum, specialUnit, state, dueDate, pickedDays, circleid, goalCreation, cancel, textRadio, disableButton }
  },
  components: { ListCircleName, TheGuestMaker},
}
</script>
<style scoped>
.tuto {
  background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);
  background-size: 200% 200%;
  animation: gradient 10s ease infinite;
  border-radius: 1em;
  padding: 3em;
  color: aliceblue;
}

@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }

  50% {
    background-position: 100% 50%;
  }

  100% {
    background-position: 0% 50%;
  }
}


input:focus {
  box-shadow: 0px 6px 10px rgba(6, 90, 92, 0.25);
  transition: all 0.2s;
  border: 1px solid rgba(6, 90, 92, 0.5);
}

textarea:focus {
  box-shadow: 0px 6px 10px rgba(6, 90, 92, 0.25);
  transition: all 0.2s;
  border: 1px solid rgba(6, 90, 92, 0.5);
}
</style>
